import React from 'react'
import {
    NavMenu,
    NavWrapper,
    NavIconWrap,
    IconMenuLateral,
    SideBar,
    IconCloseSideBar,
    NavSideBar,
    SideBarUl,
    SideBarLi,
} from "./NavElements.js"
import { Link, animateScroll as scroll } from "react-scroll";

export const Nav = ({ isOpen, setIsOpen, toggleCloseIcon }) => {

    const scrollToTop = () => {
        scroll.scrollToTop(-100);
        setIsOpen(!isOpen)
    };

    return (
        <>
            <NavMenu id="menu">
                <NavWrapper>
                    <NavIconWrap>
                        <IconMenuLateral onClick={toggleCloseIcon} />
                    </NavIconWrap>

                    <SideBar isOpen={isOpen}>
                        <IconCloseSideBar onClick={toggleCloseIcon} />

                        <NavSideBar>
                            <SideBarUl>
                                <SideBarLi>
                                    <Link className="link-menu-sidebar" activeClass="active" onClick={() => scrollToTop()} spy={true} offset={-130} smooth={true} duration={100}>Home</Link>
                                </SideBarLi>

                                <SideBarLi>
                                    <Link className="link-menu-sidebar" activeClass="active" onClick={() => setIsOpen(!isOpen)} to="container-service" spy={true} offset={-30} smooth={true} duration={100}>Services</Link>
                                </SideBarLi>

                                <SideBarLi>
                                    <Link className="link-menu-sidebar" activeClass="active" onClick={() => setIsOpen(!isOpen)} to="container-costumer" spy={true} offset={-100} smooth={true} duration={100}>Customers</Link>
                                </SideBarLi>

                                <SideBarLi>
                                    <Link className="link-menu-sidebar" activeClass="active" onClick={() => setIsOpen(!isOpen)} to="container-about" spy={true} offset={-110} smooth={true} duration={100}>About us</Link>
                                </SideBarLi>

                                <SideBarLi>
                                    <Link className="link-menu-sidebar" activeClass="active" onClick={() => setIsOpen(!isOpen)} to="container-contato" spy={true} offset={-80} smooth={true} duration={100}>Contact us</Link>
                                </SideBarLi>
                            </SideBarUl>
                        </NavSideBar>
                    </SideBar>

                </NavWrapper>
            </NavMenu>
        </>
    )
}
