import styled from "styled-components";
import { GiHamburgerMenu } from "react-icons/gi";
import { IoClose } from "react-icons/io5";
import { Link } from "react-router-dom";

export const NavMenu = styled.nav`
    width: 100%;
    height: 70px;
    background-color: transparent;
    z-index:1000000;
    position: fixed;
    padding: 0 15px;
`;

export const NavWrapper = styled.div`
    width: 100%;
    height: 70px;

    `

export const NavIconWrap = styled.div`
    width: 100%;
    height: 100%;
    
`;

export const IconMenuLateral = styled(GiHamburgerMenu)`
    cursor: pointer;
    color: #000;
    font-size: 3rem;
    float: right;
    margin-top: 10px;

    &:hover{
            color: ${props=> props.theme.ternary_color};
        }
`;

export const SideBar = styled.div`
    width: 100vw;
    height: 100vh;
    background-color: rgba(255, 255, 255);;
    position: absolute;
    z-index:1000;
    top: 0;
    right: ${({ isOpen }) => (isOpen ? '0' : '-2000px')};
    transition: .8s all;
`
export const IconCloseSideBar = styled(IoClose)`
    cursor: pointer;
    color: ${props => props.theme.primary_color};
    font-size: 4rem;
    float:right;
    background-color: #fff;


    &:hover{
        color: ${props => props.theme.ternary_color};
    }
`

export const NavSideBar = styled.nav`
    width: 100%;
    height: fit-content;
    margin-top: 50px;
    
`;

export const SideBarUl = styled.ul`
    width: 100%;
    height: fit-content;
    padding: 70px 25px;
    list-style: none;

`

export const SideBarLi = styled.li`
    width: 100%;
    height: 70px;

    .link-menu-sidebar{
        cursor: pointer;
        display: block;
        margin: 0 auto;
        text-align: center;
        font-size: 2rem;

        &:hover{
            color: ${props=> props.theme.ternary_color};
        }
    }
`
