import React from 'react'
import { Container } from './ServiceElements'
import { OwlSlider } from '../Services/SilderOwl/Index'
import { Row, Col } from "react-bootstrap"
import { Slider } from "../Services/Slider/Index"


export const Service = () => {
    return (
        <Container>

            <Row className="row">
                <Col xm={12} md={6}>
                    <div className="inner-content">
                        <OwlSlider />
                    </div>
                </Col>

                <Col xm={12} md={6}>
                    <div className="slider-col">
                        <Slider />
                    </div>
                </Col>
            </Row>


        </Container>
    )
}
