import styled from "styled-components"

export const Container = styled.div`
    width: 100%;
    height: fit-content;
    padding: 50px 0;
    text-align: center;

    h1{
        font-size: clamp(2rem, 4vw, 6rem);
        text-transform: uppercase;
        color: ${props => props.theme.ternary_color};
    }
    p{
        font-size: clamp(1rem, 4vw, 2rem);
 
    }

    .owl-theme{
        width: 100%;
        position: relative;

        .owl-nav .owl-prev,
        .owl-nav .owl-next{
            width: 40px;
            height: 120px;
            outline: none;
            background: #ffffff;
            font-size: 5rem;
            position: absolute;
            top: 35%;

            display: flex;
            justify-content: center;
            align-items: center;
        }

        .owl-nav .owl-prev{
            left: 0;
            &:hover{
                background: ${props=>props.theme.ternary_color};
            }
        }
        .owl-nav .owl-next{
            right: 0;
            &:hover{
                background: ${props=>props.theme.ternary_color};
            }
        }
        
        .item{
            width: 100%;
            height: inherit;
            cursor: pointer;
            position: relative;

            &:hover > .txt-box{
                opacity: 1;
                transition: all .9s;
            }


            img{
                width: 100%;
                height: 100%;
            }
            .txt-box{
                height: fit-content;
                opacity: 0;
                position: absolute;
                top: 75%;
                left: 40%;
                transform: translate(-50%, -50%);
                background: ${props => props.theme.ternary_color};
                color: ${props => props.theme.secundary_color};
                padding: 10px 20px;
                text-align: left;
                margin-top: 0;
                h1{
                    font-size: clamp(2rem, 2.5vw, 2.5rem);
                    color: ${props => props.theme.secundary_color};
                }
            }

        }
    }
    
`