import React from "react";
import OwlCarousel from "react-owl-carousel";
import "owl.carousel/dist/assets/owl.carousel.css";
import "owl.carousel/dist/assets/owl.theme.default.css";
import { Container } from "./SliderOwlElements.js"

const options = {
    margin: 10,
    responsiveClass: true,
    nav: true,
    dots: true,
    autoplay: false,
    smartSpeed: 1000,
    responsive: {
        0: {
            items: 1,
        },
        400: {
            items: 1,
        }
    },
};

export const OwlSlider = () => {
    return (
        <Container>
            <OwlCarousel style={{ marginTop: "80px" }} className="owl-theme" loop {...options}>
                <div className="item">
                    <h1>Serviço</h1>
                    <h2>Nós incorporamos um incrivél senso <span>artístico</span> para nossos projetos</h2>
                    <p>Nossa paixão é criar soluções na qual podemos dar um valor extra para nosso produtos, serviços ou para o ramo de negócio em geral.</p>
                </div>

                <div className="item">
                    <h1>Serviço</h1>
                    <h2>Nós incorporamos um incrivél senso <span>artístico</span> para nossos projetos</h2>
                    <p>Nossa paixão é criar soluções na qual podemos dar um valor extra para nosso produtos, serviços ou para o ramo de negócio em geral.</p>
                </div>

                <div className="item">
                    <h1>Serviço</h1>
                    <h2>Nós incorporamos um incrivél senso <span>artístico</span> para nossos projetos</h2>
                    <p>Nossa paixão é criar soluções na qual podemos dar um valor extra para nosso produtos, serviços ou para o ramo de negócio em geral.</p>
                </div>                
            </OwlCarousel>
        </Container>
    )
}