import React, {useState} from 'react'
import {Banner} from "../Components/Banner/Index"
import {Nav} from "../Components/Nav/Index"
import {Service} from "../Components/Services/Index"
import {Designer} from "../Components/DesignerContainer/Index"
import {Portfolio} from '../Components/Portfolio/Index'
import {Grid} from '../Components/Grid/Index'
import {
    BrowserRouter as Router,
    Switch,
    Route,
  } from "react-router-dom";


export const Home = () => {
    
    const [isOpen, setIsOpen] = useState(false);
    const toggleCloseIcon = () => {
      setIsOpen(!isOpen)
    }

    return (
        <>
            <Nav isOpen={isOpen} setIsOpen={setIsOpen} toggleCloseIcon={toggleCloseIcon}/>
            <Banner id="banner"/>
            <Service id="container-service"/>
            <Designer id="container-designer"/>
            <Portfolio/>
            <Grid/>
        
        </>
    )
}
