import React from 'react'
import { Container } from "./GridElements"
import {Layout} from "../Layout/Layout"
import {Row, Col} from "react-bootstrap"
import {BiMouseAlt} from "react-icons/bi"
import {MdDeveloperMode} from "react-icons/md"
import {GrApple} from "react-icons/gr"
import {IoAnalytics} from "react-icons/io5"
import {AiOutlineDeploymentUnit} from 'react-icons/ai'
import {FiMonitor} from "react-icons/fi"


export const Grid = () => {
    return (
        <Container>
            <Layout>
                <h1>O dinheiro é seu!</h1>
                <p>Nossa paixão é criar soluções na qual podemos dar um valor extra para nosso produtos, serviços ou para o ramo de negócio em geral.</p>
            </Layout>

            <Row>
                <Col xm={12} lg={4} className="coluna">
                    <BiMouseAlt className="icone"/>

                    <h2>Design</h2>
                    <p>Grandes artistas trabalhando no seu projeto, para trazer a melhor interface para a sua tecnologia.</p>
                </Col>

                <Col xm={12} lg={4} className="coluna">
                    <MdDeveloperMode className="icone"/>

                    <h2>Desenvolvimento</h2>
                    <p>Grandes artistas trabalhando no seu projeto, para trazer a melhor interface para a sua tecnologia.</p>
                </Col>

                <Col xm={12} lg={4} className="coluna">
                    <AiOutlineDeploymentUnit className="icone"/>

                    <h2>Hosteamento</h2>
                    <p>Grandes artistas trabalhando no seu projeto, para trazer a melhor interface para a sua tecnologia.</p>
                </Col>

                <Col xm={12} lg={4} className="coluna">
                    <FiMonitor className="icone"/>

                    <h2>UI / UX</h2>
                    <p>Grandes artistas trabalhando no seu projeto, para trazer a melhor interface para a sua tecnologia.</p>
                </Col>

                <Col xm={12} lg={4} className="coluna">
                    <GrApple className="icone"/>

                    <h2>Desenvolvimento IOS</h2>
                    <p>Grandes artistas trabalhando no seu projeto, para trazer a melhor interface para a sua tecnologia.</p>
                </Col>

                <Col xm={12} lg={4} className="coluna">
                    <IoAnalytics className="icone"/>

                    <h2>Markenting</h2>
                    <p>Grandes artistas trabalhando no seu projeto, para trazer a melhor interface para a sua tecnologia.</p>
                </Col>


            </Row>


        </Container>
    )
}
